import App from './controllers/App';

const app = new App();
const search = document.createElement('div');
document.body.appendChild(search);
search.innerHTML = `<div class="wrap">
<div class="search">
    <input type="text" id="name" class="searchTerm" placeholder="What are you looking for?">
    
</div>
</div>"`;
const searchButton = document.createElement('div');
search.appendChild(searchButton);
searchButton.innerHTML = `<div class="wrap">

    <button type="submit" class="searchButton" onclick="app.start()">
        <i class="fa fa-search"></i>
    </button>`;


searchButton.addEventListener('click', () => {
  const searchQuery = document.getElementById('name').value;
  app.start(searchQuery);
});
