export default class App {
  constructor() {
    this.state = {
      url: '0',
    };
  }

  async start(searchQuery) {
    global.console.log('waaaaath', searchQuery);
    this.state.url = `https://www.googleapis.com/youtube/v3/search?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&type=video&part=snippet&maxResults=15&q='+ ${searchQuery}`;
    const model = new AppModel(this.state);
    const data = await model.getClipNames();
    const viewCount = await model.getViewCount();
    const view = new AppView(data, viewCount);
    view.render();
  }
}
