export default class AppModel {
  constructor(state) {
    this.state = state;
    this.views = {};
  }

  static extractClipNames(data) {
    global.console.log(data.items.map(clip => clip.snippet));
    return data.items.map(clip => clip.snippet);
  }

  async getClipNames() {
    const { url } = this.state;
    const response = await fetch(url);
    global.console.log(url);
    const data = await response.json();
    return AppModel.extractClipNames(data);
  }

  async getViewCount() {
    const { url } = this.state;
    const response = await fetch(url);
    const data = await response.json();
    const videoIdArray = data.items.map(val => val.id.videoId);
    const idStr = videoIdArray.join(',');
    const response2 = await fetch(`https://www.googleapis.com/youtube/v3/videos?key=AIzaSyCTWC75i70moJLzyNh3tt4jzCljZcRkU8Y&id=${idStr}&part=snippet,statistics`);
    const data2 = await response2.json();
    global.console.log('w', data2);
    return data2.items.map(val => val.statistics);
  }
}
