export default class AppView {
  constructor(data, viewCount) {
    this.data = data;
    this.viewCount = viewCount;
  }

  render() {
    global.console.log('jj', this.viewCount);
    const queryDataObject = {
      key: 'AIzaSyC7FaETWWZQX7Z3A-qpjZ9yF1FG2Yh41DQ',
      type: 'video',
      part: 'snippet',
      count: '12',
    };
    const createNote = `<div class="imageBox">
    <div class="title"><a href="" target="_blank"></a></div>
    </div>
    <div class="text">
      <div class="info">
        <div class="info-container">
          <div class="iconBox">
            <img src="../src/img/user.png" alt="Channel name">
          </div>
          <div class="channelData"></div>
        </div>
        <div class="info-container">
          <div class="iconBox">
            <img src="../src/img/date.png" alt="Channel date">
          </div>
          <div class="channelData"></div>
        </div>
        <div class="info-container">
          <div class="iconBox">
            <img src="../src/img/views.png" alt="Channel views">
          </div>
          <div class="channelData"></div>
        </div>
      </div>
      <div class="description"></div>
    </div>`;

    const main = `<main>
      <div class="form">
      </div>
      <div class="slider">
        

        <div class="slide" id='slide2'>
          <div class="page">
            <div class="resultBox">
              <div class="components"></div>
            </div>
          </div>
        </div>

        <div class="slide" id='slide3'>
          <div class="page">
            <div class="resultBox">
              <div class="components"></div>
            </div>
          </div>
        </div>

        <div class="slide" id='slide4'>
          <div class="page">
            <div class="resultBox">
              <div class="components"></div>
            </div>
          </div>
        </div>
      </div>
      <button class="control1" id="previous">Previous</button>
      <button class="control2" id="next">Next</button>
    </main>`;

    const c = document.createElement('div');
    document.body.appendChild(c);
    c.innerHTML = main;

    function initializeNote(currentElement, items, viewCount) {
      const imageBox = currentElement.querySelector('.imageBox');
      const imageUrl = items.thumbnails.medium.url;
      imageBox.style = `background: url(${imageUrl}) no-repeat;`;
      const title = imageBox.querySelector('a');
      title.href = `https://www.youtube.com/watch?v=${items.id}`;
      title.innerHTML = items.title;
      const channelData = currentElement.querySelectorAll('.channelData');
      channelData[0].innerHTML = items.channelTitle;
      const date = items.publishedAt.split('T')[0];
      channelData[1].innerHTML = date;
      channelData[2].innerHTML = viewCount.viewCount;
      const description = currentElement.querySelector('.description');
      const descriptionText = items.description.slice(0, 100);
      description.textContent = `${descriptionText}...`;
    }
    const slides = document.querySelectorAll('.slide');
    let currentSlide = 0;

    const next = document.getElementById('next');
    const previous = document.getElementById('previous');
    function goToSlide(n) {
      slides[currentSlide].className = 'slide';
      currentSlide = (n + slides.length) % slides.length;
      slides[currentSlide].className = 'slide showing';
    }
    function nextSlide() {
      goToSlide(currentSlide + 1);
    }
    function previousSlide() {
      goToSlide(currentSlide - 1);
    }

    next.addEventListener('click', () => {
    // pauseSlideshow();
      nextSlide();
    });

    previous.addEventListener('click', () => {
    //  pauseSlideshow();
      previousSlide();
    });

    function createNewNote(index, components) {
      const noteDiv = document.createElement('div');
      noteDiv.className = 'note';
      if (index % 4 === 2 || index % 4 === 3) {
        noteDiv.className += ' hide';
      }
      components.appendChild(noteDiv);
      noteDiv.innerHTML = createNote;
    }

    function createAnswer(notes, viewCount) {
      const components = document.querySelectorAll('.components');
      if (components.textContent !== 'undefined') {
        let numSlide = 0;
        for (let i = 0, n = queryDataObject.count; i < n; i += 1) {
          createNewNote(i, components[numSlide]);
          if (i % 4 === 3) {
            numSlide += 1;
          }
        }
      }
      const elements = document.querySelectorAll('.note');
      for (let i = 0, n = queryDataObject.count; i < n; i += 1) {
        initializeNote(elements[i], notes[i], viewCount[i]);
      }
      nextSlide();
      return null;
    }

    createAnswer(this.data, this.viewCount);
  }
}
